import { useSelector } from "react-redux";
import { useSearchParams } from "react-router-dom";
import InstituteNavbarConfig from "../InstituteMember/Config/InstituteNavbarConfig";
import InstituteMemberConfig from "../InstituteMember/Config/InstituteMemberConfig";
import GoBack from "../Shared/StaffStudentProfile/GoBack";
import { Suspense, useEffect } from "react";
import QvipleLoader from "../Loader/QvipleLoader";
import SiteHomeOpener from "./HomeOpener/SiteHomeOpener";
import SiteTestimonial from "./SiteTestimonial/SiteTestimonial";
import IsoCertificate from "./IsoCertificate/IsoCertificate";
import LandingControl from "./LandingControl/LandingControl";
import HomeFounders from "./HomeFounders/HomeFounders";
import Accreditation from "./Accreditation/Accreditation";
import ImageGallary from "./ImageGallary/ImageGallary";
import SiteAffiliation from "./SiteAffiliation/SiteAffiliation";
import SocietyTrust from "./SocietyTrust/SocietyTrust";
import { useOneInstituteDashboard } from "../../hooks/dashboard/dashboard-api";
import SiteAcademicCourses from "./SiteAcademicCourses/SiteAcademicCourses";
import SiteHome from "./SiteHome/SiteHome";
import HomeHeader from "./SiteHome/HomeHeader";
import HomeBackgroundTheme from "./SiteHome/HomeBackgroundTheme";
import HomeAboutInstitute from "./SiteHome/HomeAboutInstitute";
import HomeQuickLinks from "./SiteHome/HomeQuickLinks";
import HomeVideoGallery from "./SiteHome/HomeVideoGallery/HomeVideoGallery";
import HomeImageGallery from "./SiteHome/HomeImageGallery/HomeImageGallery";
import HomeFooter from "./SiteHome/HomeFooter/HomeFooter";
import HomeAccreditation from "./SiteHome/HomeAccreditation/HomeAccreditation";
import SiteAboutUs from "./SiteAboutUs/SiteAboutUs";

const InstituteOtherSiteConfig = () => {
  const [searchParams] = useSearchParams();
  const instituteId = useSelector((state) => state.idChange.id);

  const { oneInstituteDashboard, oneInstituteDashboardRefetch } =
    useOneInstituteDashboard({
      id: instituteId,
      skip: !instituteId,
    });
  useEffect(() => {
    if (instituteId) oneInstituteDashboardRefetch();
  }, [instituteId, oneInstituteDashboardRefetch]);

  return (
    <InstituteNavbarConfig>
      <InstituteMemberConfig>
        <section
          style={{
            width: "100%",
          }}
        >
          <GoBack
            customStyle={{
              width: "100%",
              marginTop: "0",
              marginBottom: "1rem",
            }}
          />
          <Suspense fallback={<QvipleLoader />}>
            {searchParams.get("type") === "home-opener" ? (
              <SiteHomeOpener instituteId={instituteId} />
            ) : searchParams.get("type") === "testimonial" ? (
              <SiteTestimonial instituteId={instituteId} />
            ) : searchParams.get("type") === "iso" ? (
              <IsoCertificate
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "landing-control" ? (
              <LandingControl
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "founder" ? (
              <HomeFounders
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "accreditation" ? (
              <Accreditation
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "gallary" ? (
              <ImageGallary
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "affiliation" ? (
              <SiteAffiliation
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "society-trust" ? (
              <SocietyTrust
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "academic-courses" ? (
              <SiteAcademicCourses
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "home" ? (
              <SiteHome
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "header" ? (
              <HomeHeader
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "theme" ? (
              <HomeBackgroundTheme
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "home-institute" ? (
              <HomeAboutInstitute
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "home-links" ? (
              <HomeQuickLinks
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "video-gallery" ? (
              <HomeVideoGallery
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "image-gallery" ? (
              <HomeImageGallery
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "home-footer" ? (
              <HomeFooter
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "home-accreditation" ? (
              <HomeAccreditation
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "aboutus" ? (
              <SiteAboutUs
                instituteId={instituteId}
                lcid={oneInstituteDashboard?.institute?.landing_control}
                // lcid="661333967aa7af061e0f0aaa"
              />
            ) : null}
          </Suspense>
        </section>
      </InstituteMemberConfig>
    </InstituteNavbarConfig>
  );
};

export default InstituteOtherSiteConfig;
