import { useTranslation } from "react-i18next";
import style from "../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useState } from "react";
import HomeVideoGalleryCardMenu from "./HomeVideoGalleryCardMenu";
const HomeVideoGalleryCard = ({ founder, onRefetch, lcid }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);
  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div
        className={style.sf_card}
        style={{
          alignItems: "flex-start",
        }}
      >
        <div
          className={style.sf_card_contatiner}
          style={{
            alignItems: "flex-start",
          }}
        >
          <img
            src={
              founder?.image
                ? `${imageShowUrl}/${founder?.image}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            style={{
              borderRadius: "6px",
              width: "5rem",
              height: "5rem",
            }}
            alt="user avatar"
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6
              style={{
                marginBottom: "0",
              }}
            >
              {t("video_link")}
              {": "}
              <a
                href={founder?.link}
                target="_blank"
                rel="noopener noreferrer"
                style={{
                  textDecoration: "none",
                  color: "#0942a6",
                  wordBreak: "break-all",
                }}
              >
                {founder?.link ?? ""}
              </a>
            </h6>
            <p>{founder?.description ?? ""}</p>
          </div>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onToggle}
        />
      </div>

      {open && (
        <HomeVideoGalleryCardMenu
          onClose={onToggle}
          lcid={lcid}
          menuData={founder}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default HomeVideoGalleryCard;
