import { useState } from "react";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInput from "../../../Ui/Input/FormInput";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import style from "../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import FormFile from "../../../Ui/Input/FormFile";
import { useFileUpload } from "../../../../hooks/authentication-api";
import { useSiteHomeAccreditationCertificate } from "../../../../hooks/setting-api";
const AddHomeAccreditationDetailt = ({ onClose, onRefetch, acid, lcid }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    c_name: "",
    c_attach: "",
  });

  const [fileUpload] = useFileUpload();
  const [siteHomeAccreditationCertificate] =
    useSiteHomeAccreditationCertificate();

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            c_attach: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && lcid && acid) {
      setDisabled((pre) => !pre);
      siteHomeAccreditationCertificate({
        lcid: lcid,
        acid: acid,
        bodyParameter: state,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("add_certificate")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("certificate_name")}
              placeholder={t("certificate_name_placeholder")}
              name={"c_name"}
              value={state.c_name}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["c_name"]}
            />
            <FormFile
              label={t("attachment")}
              name={"attachment"}
              value={
                state.c_attach
                  ? {
                      key: state.c_attach,
                      name: "View",
                    }
                  : ""
              }
              onChange={onFileUpload}
              errorShow={errorField["c_attach"]}
            />
            <CreateButton label="add" onAction={onAdd} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default AddHomeAccreditationDetailt;
