import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../Ui/Border/BorderBottom";
import { useParams, useSearchParams } from "react-router-dom";
import CustomSquareTabCard from "../../Ui/Tab/Square/CustomSquareTabCard";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import SquareTabWrapper from "../../Ui/Tab/Square/SquareTabWrapper";
import AboutUsSociety from "./AboutUsSociety";
import AboutUsInstituteAbout from "./AboutUsInstituteAbout";
import AboutUsAdministration from "./AboutUsAdministration/AboutUsAdministration";
const SiteAboutUs = ({ instituteId, lcid }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const params = useParams();
  let measure = document.getElementById("562ntty")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    let disvible =
      measure > 820 ? (measure > 1100 ? 6 : 5) : measure > 700 ? 4 : 3;
    let mi = measure > 820 ? 0 : 2;
    width = `${Math.floor((measure - mi - 31 * disvible - 1) / disvible)}px`;
    height = width;
  }
  return (
    <>
      {searchParams.get("n") === "society" ? (
        <AboutUsSociety instituteId={instituteId} lcid={lcid} />
      ) : searchParams.get("n") === "institute" ? (
        <AboutUsInstituteAbout instituteId={instituteId} lcid={lcid} />
      ) : searchParams.get("n") === "administration" ? (
        <AboutUsAdministration instituteId={instituteId} lcid={lcid} />
      ) : searchParams.get("type") === "aboutus" ? (
        <UserMemberContainerWrapper>
          <UserMemberContentWrapper>
            <div className={style.fm_header_container}>
              <h6>{t("about_us")}</h6>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <SquareTabWrapper dynamicId="562ntty">
            <CustomSquareTabCard
              label={"about_society"}
              iconUrl={`${assestsUserFinanceUrl}/bank.svg`}
              url={`/q/${params.username}/other/site?type=aboutus&n=society`}
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <CustomSquareTabCard
              label={"about_institute"}
              iconUrl={`${assestsUserFinanceUrl}/bank.svg`}
              url={`/q/${params.username}/other/site?type=aboutus&n=institute`}
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <CustomSquareTabCard
              label={"administration"}
              iconUrl={`${assestsUserFinanceUrl}/bank.svg`}
              url={`/q/${params.username}/other/site?type=aboutus&n=administration`}
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
          </SquareTabWrapper>
        </UserMemberContainerWrapper>
      ) : null}
    </>
  );
};

export default SiteAboutUs;
