import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import { useFileUpload } from "../../../hooks/authentication-api";
import {
  existingRequiredField,
  requiredField,
} from "../../Validation/functions/checkingRequiredField";
import QvipleLoader from "../../Loader/QvipleLoader";
import { Dialog } from "@mui/material";
import CustomCrop from "../../../ImageCrop/CustomCrop";
import CreateButton from "../../Ui/Button/CreateButton";
import FormFile from "../../Ui/Input/FormFile";
import FormInput from "../../Ui/Input/FormInput";
import FormTextarea from "../../Ui/Input/FormTextarea";
import {
  useSiteHomeHeader,
  useWebsiteInfoGetByInstitute,
} from "../../../hooks/setting-api";

const HomeHeader = ({ instituteId, lcid }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    ins_name: "",
    ins_logo: "",
    affiliation_logo: "",
    ins_address: "",
    affiliation_with: "",
    ins_email: "",
    ins_phone_number: "",
    accreditations: "",
  });
  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");
  const [activeCrop, setActiveCrop] = useState("");
  const [fileUpload] = useFileUpload();
  const [siteHomeHeader] = useSiteHomeHeader();

  const { websiteInfoGetByInstitute, websiteInfoGetByInstituteRefetch } =
    useWebsiteInfoGetByInstitute({
      id: instituteId,
      skip: !instituteId,
    });

  useEffect(() => {
    if (instituteId) {
      websiteInfoGetByInstituteRefetch();
    }
  }, [instituteId]);

  useEffect(() => {
    if (
      websiteInfoGetByInstitute?.one_ins?.landing_control?.home_header_object
    ) {
      let st =
        websiteInfoGetByInstitute?.one_ins?.landing_control?.home_header_object;
      setState({
        ins_name: st?.ins_name ?? "",
        ins_logo: st?.ins_logo ?? "",
        affiliation_logo: st?.affiliation_logo ?? "",
        ins_address: st?.ins_address ?? "",
        affiliation_with: st?.affiliation_with ?? "",
        ins_email: st?.ins_email ?? "",
        ins_phone_number: st?.ins_phone_number ?? "",
        accreditations: st?.accreditations ?? "",
      });
    }
  }, [websiteInfoGetByInstitute?.one_ins?.landing_control?.home_header_object]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onCrop = (fl, url) => {
    setOpenCrop(false);
    if (activeCrop === "LOGO") onLogoUploadCrop(fl);
    else onAffiliationUploadCrop(fl);
  };

  const onLogoUpload = (e) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
      setActiveCrop("LOGO");
    }
  };
  const onLogoUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            ins_logo: res.data?.imageKey,
          }));
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onAffiliationUpload = (e) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
      setActiveCrop("AFFILIATION");
    }
  };
  const onAffiliationUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            affiliation_logo: res.data?.imageKey,
          }));
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onUpdate = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && lcid) {
      setDisabled((pre) => !pre);
      siteHomeHeader({
        lcid: lcid,
        bodyParameter: {
          home_header_object: state,
        },
      })
        .then(() => {
          websiteInfoGetByInstituteRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("header")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          <div className={style.ossa_form}>
            <FormFile
              label={t("institute_logo")}
              name={"ins_logo"}
              value={
                state.ins_logo
                  ? {
                      key: state.ins_logo,
                      name: "View",
                    }
                  : ""
              }
              onChange={onLogoUpload}
              errorShow={errorField["ins_logo"]}
              customFormFileContainer={{
                width: "49%",
              }}
            />
            <FormFile
              label={t("affiliation_logo")}
              name={"affiliation_logo"}
              value={
                state.affiliation_logo
                  ? {
                      key: state.affiliation_logo,
                      name: "View",
                    }
                  : ""
              }
              onChange={onAffiliationUpload}
              errorShow={errorField["affiliation_logo"]}
              customFormFileContainer={{
                width: "49%",
              }}
            />
            <FormInput
              label={t("institute_name")}
              placeholder={t("institute_name_placeholder")}
              name={"ins_name"}
              value={state.ins_name}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["ins_name"]}
              customFormInputContainer={{ width: "49%" }}
            />

            <FormTextarea
              label={t("institute_address")}
              placeholder={t("institute_address_placeholder")}
              name={"ins_address"}
              value={state.ins_address}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["ins_address"]}
              customFormInputContainer={{ width: "49%" }}
            />
            <FormInput
              label={t("institute_email")}
              placeholder={t("institute_email_placeholder")}
              name={"ins_email"}
              value={state.ins_email}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["ins_email"]}
              customFormInputContainer={{ width: "49%" }}
            />
            <FormInput
              label={t("institute_contact_number")}
              placeholder={t("institute_contact_number_placeholder")}
              name={"ins_phone_number"}
              value={state.ins_phone_number}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["ins_phone_number"]}
              customFormInputContainer={{ width: "49%" }}
            />

            <FormInput
              label={t("affiliated_with")}
              placeholder={t("affiliated_with_placeholder")}
              name={"affiliation_with"}
              value={state.affiliation_with}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["affiliation_with"]}
              customFormInputContainer={{ width: "49%" }}
            />
            <FormTextarea
              label={t("accreditation")}
              placeholder={t("accreditation_placeholder")}
              name={"accreditations"}
              value={state.accreditations}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["accreditations"]}
              customFormInputContainer={{ width: "49%" }}
            />
          </div>

          <CreateButton label="update" onAction={onUpdate} />
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {openCrop ? (
        <Dialog open={openCrop}>
          <CustomCrop
            setOpenCrop={setOpenCrop}
            fileUrl={fileUrl}
            first={1}
            second={1}
            onCrop={onCrop}
          />
        </Dialog>
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default HomeHeader;
