import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInput from "../../../Ui/Input/FormInput";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import style from "../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { useInstituteAddSubHeadAcademic } from "../../../../hooks/setting-api";

const AddSiteAcademicCoursesDetail = ({
  acid,
  onClose,
  isEdit,
  onRefetch,
  menuData,
  onEditAction,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    sub_head_title_main: "",
  });
  // const [activeCrop, setActiveCrop] = useState("");

  const [instituteAddSubHeadAcademic] = useInstituteAddSubHeadAcademic();

  useEffect(() => {
    if (isEdit && menuData) {
      setState({
        sub_head_title_main: menuData?.sub_head_title_main ?? "",
      });
    }
  }, [isEdit, menuData]);
  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && acid) {
      if (isEdit) {
        onEditAction(state);
      } else {
        setDisabled((pre) => !pre);
        instituteAddSubHeadAcademic({
          acid: acid,
          bodyParameter: state,
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>
                  {t(isEdit ? "update_programm_title" : "add_programm_title")}
                </h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("title")}
              placeholder={t("title_placeholder")}
              name={"sub_head_title_main"}
              value={state.sub_head_title_main}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["sub_head_title_main"]}
            />
            <CreateButton label={isEdit ? "update" : "add"} onAction={onAdd} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default AddSiteAcademicCoursesDetail;
