import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import { useFileUpload } from "../../../hooks/authentication-api";
import {
  existingRequiredField,
  requiredField,
} from "../../Validation/functions/checkingRequiredField";
import QvipleLoader from "../../Loader/QvipleLoader";
import { Dialog } from "@mui/material";
import CustomCrop from "../../../ImageCrop/CustomCrop";
import CreateButton from "../../Ui/Button/CreateButton";
import FormFile from "../../Ui/Input/FormFile";
import FormInput from "../../Ui/Input/FormInput";
import FormTextarea from "../../Ui/Input/FormTextarea";
import {
  useSiteHomeAbout,
  useWebsiteInfoGetByInstitute,
} from "../../../hooks/setting-api";

import Switch from "@mui/material/Switch";
const label = { inputProps: { "aria-label": "Switch demo" } };

const HomeAboutInstitute = ({ instituteId, lcid }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    about: "",
    typo: false,
    link_images: "",
  });
  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");

  const [fileUpload] = useFileUpload();
  const [siteHomeAbout] = useSiteHomeAbout();

  const { websiteInfoGetByInstitute, websiteInfoGetByInstituteRefetch } =
    useWebsiteInfoGetByInstitute({
      id: instituteId,
      skip: !instituteId,
    });

  useEffect(() => {
    if (instituteId) {
      websiteInfoGetByInstituteRefetch();
    }
  }, [instituteId]);

  useEffect(() => {
    if (
      websiteInfoGetByInstitute?.one_ins?.landing_control
        ?.home_about_institute_object
    ) {
      let st =
        websiteInfoGetByInstitute?.one_ins?.landing_control
          ?.home_about_institute_object;
      setState({
        about: st?.about ?? "",
        typo: st?.typo ?? "",
        link_images: st?.link_images ?? "",
      });
    }
  }, [
    websiteInfoGetByInstitute?.one_ins?.landing_control
      ?.home_about_institute_object,
  ]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onCrop = (fl, url) => {
    setOpenCrop(false);
    onImageUploadCrop(fl);
  };

  const onImageUpload = (e) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
    }
  };
  const onImageUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            link_images: res.data?.imageKey,
          }));
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onUpdate = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && lcid) {
      setDisabled((pre) => !pre);
      siteHomeAbout({
        lcid: lcid,
        bodyParameter: {
          home_about_institute_object: {
            ...state,
            typo: state.typo ? "YES" : "NO",
          },
        },
      })
        .then(() => {
          websiteInfoGetByInstituteRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("about_institute")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          <div className={style.ossa_form}>
            <div
              className={style.sfscc_container}
              style={{
                width: "49%",
              }}
            >
              <Switch
                {...label}
                checked={state.typo}
                onChange={(e) =>
                  setState((prev) => ({
                    ...prev,
                    typo: e.target.checked,
                    link_images: "",
                  }))
                }
              />
              <span>{t("is_it_a_link")}</span>
            </div>
            {state.typo ? (
              <FormInput
                label={t("video_link")}
                placeholder={t("video_link_placeholder")}
                name={"link_images"}
                value={state.link_images}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["link_images"]}
                customFormInputContainer={{ width: "49%" }}
              />
            ) : (
              <FormFile
                label={t("about_image")}
                name={"about_image"}
                value={
                  state.link_images
                    ? {
                        key: state.link_images,
                        name: "View",
                      }
                    : ""
                }
                onChange={onImageUpload}
                errorShow={errorField["link_images"]}
                customFormFileContainer={{
                  width: "49%",
                }}
              />
            )}

            <FormTextarea
              label={t("institute_about")}
              placeholder={t("institute_about_placeholder")}
              name={"about"}
              value={state.about}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["about"]}
              customFormInputContainer={{ width: "100%" }}
            />
          </div>

          <CreateButton label="update" onAction={onUpdate} />
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {openCrop ? (
        <Dialog open={openCrop}>
          <CustomCrop
            setOpenCrop={setOpenCrop}
            fileUrl={fileUrl}
            first={3}
            second={4}
            onCrop={onCrop}
          />
        </Dialog>
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default HomeAboutInstitute;
