import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInput from "../../../Ui/Input/FormInput";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import style from "../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import FormFile from "../../../Ui/Input/FormFile";
import { useFileUpload } from "../../../../hooks/authentication-api";
import { Dialog } from "@mui/material";
import CustomCrop from "../../../../ImageCrop/CustomCrop";
import { useSiteHomeAccreditation } from "../../../../hooks/setting-api";
import FormTextarea from "../../../Ui/Input/FormTextarea";
const AddHomeAccreditation = ({
  onClose,
  onRefetch,
  lcid,
  isEdit,
  menuData,
  onEditAction,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    name: "",
    image: "",
    about: "",
  });
  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");
  const [siteHomeAccreditation] = useSiteHomeAccreditation();

  const [fileUpload] = useFileUpload();

  useEffect(() => {
    if (isEdit && menuData) {
      setState({
        name: menuData?.name ?? "",
        image: menuData?.image ?? "",
        about: menuData?.about ?? "",
      });
    }
  }, [isEdit, menuData]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onCrop = (fl, url) => {
    setOpenCrop(false);
    onFileUploadCrop(fl);
  };

  const onFileUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            image: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
    }
  };

  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && lcid) {
      if (isEdit) {
        onEditAction(state);
      } else {
        setDisabled((pre) => !pre);
        siteHomeAccreditation({
          lcid: lcid,
          bodyParameter: state,
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      {openCrop ? (
        <Dialog open={openCrop}>
          <CustomCrop
            setOpenCrop={setOpenCrop}
            fileUrl={fileUrl}
            first={1}
            second={1}
            onCrop={onCrop}
          />
        </Dialog>
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>
                    {t(isEdit ? "update_accreditation" : "add_accreditation")}
                  </h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("accrediation_name")}
                placeholder={t("accrediation_name_placeholder")}
                name={"name"}
                value={state.name}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["name"]}
              />
              <FormFile
                label={t("add_image")}
                name={"home-opener"}
                value={
                  state.image
                    ? {
                        key: state.image,
                        name: "View",
                      }
                    : ""
                }
                onChange={onFileUpload}
                errorShow={errorField["image"]}
              />
              <FormTextarea
                label={t("accrediation_about")}
                placeholder={t("accrediation_about_placeholder")}
                name={"about"}
                value={state.about}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["about"]}
              />
              <CreateButton
                label={isEdit ? "update" : "add"}
                onAction={onAdd}
              />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AddHomeAccreditation;
