import { useTranslation } from "react-i18next";
import FormFileView from "../../Ui/Input/FormFileView";
import style from "../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import { useState } from "react";
import SiteHomeOpenerCardMenu from "./SiteHomeOpenerCardMenu";
const SiteHomeOpenerCard = ({ hOpener, onRefetch, instituteId }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);

  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div className={style.sf_card}>
        <div className={style.sf_card_contatiner}>
          <div className={style.sf_card_contatiner_inner}>
            <h6
              style={{
                marginBottom: "0",
              }}
            >
              {t("link")}
              {": "}
              <a
                href={hOpener?.link}
                target="_blank"
                rel="noopener noreferrer"
                style={{
                  textDecoration: "none",
                  color: "#0942a6",
                }}
              >
                {hOpener?.link ?? ""}
              </a>
            </h6>
            <FormFileView
              label={`${t("image")}: `}
              value={{
                key: hOpener?.image,
              }}
              customFormInputContainer={{
                margin: "0",
              }}
            />
          </div>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onToggle}
        />
      </div>

      {open && (
        <SiteHomeOpenerCardMenu
          onClose={onToggle}
          onRefetch={onRefetch}
          instituteId={instituteId}
          data={hOpener}
        />
      )}
    </>
  );
};

export default SiteHomeOpenerCard;
