import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
import FormInput from "../../../Ui/Input/FormInput";
import {
  useSiteHomeFooter,
  useWebsiteInfoGetByInstitute,
} from "../../../../hooks/setting-api";

const HomeFooter = ({ instituteId, lcid }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    linkedin_link: "",
    instagram_link: "",
    twitter_link: "",
    qviple_link: "",
    youtube_link: "",
    facebook_link: "",
  });

  const [siteHomeFooter] = useSiteHomeFooter();

  const { websiteInfoGetByInstitute, websiteInfoGetByInstituteRefetch } =
    useWebsiteInfoGetByInstitute({
      id: instituteId,
      skip: !instituteId,
    });

  useEffect(() => {
    if (instituteId) {
      websiteInfoGetByInstituteRefetch();
    }
  }, [instituteId]);
  useEffect(() => {
    if (websiteInfoGetByInstitute?.one_ins?.landing_control?.footer_links) {
      let st =
        websiteInfoGetByInstitute?.one_ins?.landing_control?.footer_links;
      setState({
        linkedin_link: st?.linkedin_link ?? "",
        instagram_link: st?.instagram_link ?? "",
        twitter_link: st?.twitter_link ?? "",
        qviple_link: st?.qviple_link ?? "",
        youtube_link: st?.youtube_link ?? "",
        facebook_link: st?.facebook_link ?? "",
      });
    }
  }, [websiteInfoGetByInstitute?.one_ins?.landing_control?.footer_links]);
  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onUpdate = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && lcid) {
      setDisabled((pre) => !pre);
      siteHomeFooter({
        lcid: lcid,
        bodyParameter: {
          footer_links: state,
        },
      })
        .then(() => {
          websiteInfoGetByInstituteRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("footer")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          <div className={style.ossa_form}>
            <FormInput
              label={t("site_linkedin")}
              placeholder={t("site_linkedin_placeholder")}
              name={"linkedin_link"}
              value={state.linkedin_link}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["linkedin_link"]}
              customFormInputContainer={{ width: "49%" }}
            />

            <FormInput
              label={t("site_instagram")}
              placeholder={t("site_instagram_placeholder")}
              name={"instagram_link"}
              value={state.instagram_link}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["instagram_link"]}
              customFormInputContainer={{ width: "49%" }}
            />
            <FormInput
              label={t("site_twitter")}
              placeholder={t("site_twitter_placeholder")}
              name={"twitter_link"}
              value={state.twitter_link}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["twitter_link"]}
              customFormInputContainer={{ width: "49%" }}
            />

            <FormInput
              label={t("site_qviple_profile")}
              placeholder={t("site_qviple_profile_placeholder")}
              name={"qviple_link"}
              value={state.qviple_link}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["qviple_link"]}
              customFormInputContainer={{ width: "49%" }}
            />
            <FormInput
              label={t("site_youtube")}
              placeholder={t("site_youtube_placeholder")}
              name={"youtube_link"}
              value={state.youtube_link}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["youtube_link"]}
              customFormInputContainer={{ width: "49%" }}
            />
            <FormInput
              label={t("site_facebook")}
              placeholder={t("site_facebook_placeholder")}
              name={"facebook_link"}
              value={state.facebook_link}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["facebook_link"]}
              customFormInputContainer={{ width: "49%" }}
            />
          </div>

          <CreateButton label="update" onAction={onUpdate} />
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>

      {disabled && <QvipleLoader />}
    </>
  );
};

export default HomeFooter;
