import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useState } from "react";
import { useSiteHomeAccreditationCertificateList } from "../../../../hooks/setting-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useLocation } from "react-router-dom";
import AddHomeAccreditationDetailt from "./AddHomeAccreditationDetailt";
import Loading from "../../../Loader/Loading";
import HomeAccreditationDetailtCard from "./HomeAccreditationDetailtCard";

const HomeAccreditationDetailt = ({ lcid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const getQuery = useLocation();

  const {
    siteHomeAccreditationCertificateList,
    siteHomeAccreditationCertificateListRefetch,
    siteHomeAccreditationCertificateListLoading,
  } = useSiteHomeAccreditationCertificateList({
    data: {
      lcid: lcid,
      hid: getQuery?.state?.accrediation?._id,
    },
    skip: lcid ? !getQuery?.state?.accrediation?._id : !lcid,
  });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  //
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{getQuery?.state?.accrediation?.name ?? ""}</h6>
            <CreateButton
              label="add"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {siteHomeAccreditationCertificateList?.map((certi) => (
            <HomeAccreditationDetailtCard key={certi?._id} certi={certi} />
          ))}

          {siteHomeAccreditationCertificateListLoading && <Loading />}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {/* {disabled && <QvipleLoader />} */}
      {add && (
        <AddHomeAccreditationDetailt
          onClose={onToggle}
          lcid={lcid}
          onRefetch={siteHomeAccreditationCertificateListRefetch}
          acid={getQuery?.state?.accrediation?._id}
        />
      )}
    </>
  );
};

export default HomeAccreditationDetailt;
