import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../Validation/functions/checkingRequiredField";
import Modal from "../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../Ui/Border/BorderBottom";
import FormInput from "../../Ui/Input/FormInput";
import CreateButton from "../../Ui/Button/CreateButton";
import QvipleLoader from "../../Loader/QvipleLoader";
import style from "../../UserStaffMember/FinanceManager/Income/Income.module.css";
import FormFile from "../../Ui/Input/FormFile";
import { useFileUpload } from "../../../hooks/authentication-api";
import { Dialog } from "@mui/material";
import CustomCrop from "../../../ImageCrop/CustomCrop";
import { useInstituteUpdateHomeOpener } from "../../../hooks/setting-api";
const EditHomeOpener = ({
  instituteId,
  onClose,
  onRefetch,
  onParentClose,
  data,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    linkUrl: "",
    image: "",
    q_link1: "",
    q_link2: "",
    q_link3: "",
  });
  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");
  const [instituteUpdateHomeOpener] = useInstituteUpdateHomeOpener();

  const [fileUpload] = useFileUpload();

  useEffect(() => {
    if (data) {
      setState({
        linkUrl: data?.link ?? "",
        image: data?.image ?? "",
        q_link1: data?.quick_links?.[0] ?? "",
        q_link2: data?.quick_links?.[1] ?? "",
        q_link3: data?.quick_links?.[2] ?? "",
      });
    }
  }, [data]);
  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onCrop = (fl, url) => {
    setOpenCrop(false);
    onFileUploadCrop(fl);
  };

  const onFileUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            image: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
    }
  };

  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && instituteId && data?._id) {
      setDisabled((pre) => !pre);
      let lt = [];
      if (state.q_link1) lt.push(state.q_link1);
      if (state.q_link2) lt.push(state.q_link2);
      if (state.q_link3) lt.push(state.q_link3);

      instituteUpdateHomeOpener({
        id: instituteId,
        hid: data?._id,
        bodyParameter: {
          link: state.linkUrl,
          image: state.image,
          quick: lt,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onParentClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      {openCrop ? (
        <Dialog open={openCrop}>
          <CustomCrop
            setOpenCrop={setOpenCrop}
            fileUrl={fileUrl}
            first={4}
            second={3}
            onCrop={onCrop}
          />
        </Dialog>
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("update_home_opener")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("linkUrl")}
                placeholder={t("linkUrl_placeholder")}
                name={"linkUrl"}
                value={state.linkUrl}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["linkUrl"]}
              />
              <FormFile
                label={t("add_image")}
                name={"home-opener"}
                value={
                  state.image
                    ? {
                        key: state.image,
                        name: "View",
                      }
                    : ""
                }
                onChange={onFileUpload}
                errorShow={errorField["image"]}
              />
              <FormInput
                label={t("quick_link1")}
                placeholder={t("quick_link_placeholder")}
                name={"q_link1"}
                value={state.q_link1}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["q_link1"]}
              />
              <FormInput
                label={t("quick_link2")}
                placeholder={t("quick_link_placeholder")}
                name={"q_link2"}
                value={state.q_link2}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["q_link2"]}
              />
              <FormInput
                label={t("quick_link3")}
                placeholder={t("quick_link_placeholder")}
                name={"q_link3"}
                value={state.q_link3}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["q_link3"]}
              />
              <CreateButton label="add" onAction={onAdd} />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default EditHomeOpener;
