import { useTranslation } from "react-i18next";
import ModalMenu from "../../Ui/Modal/ModalMenu";
import { useState } from "react";
import {
  useAcademicRemove,
  useAcademicUpdate,
} from "../../Hooks/ApiHooks/SiteManager/site-manager-api";
import AddSiteAcademicsCourses from "./AddSiteAcademicCourses";
const SiteAcademicCoursesMenu = ({ lcid, onClose, menuData, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");

  const [academicRemove] = useAcademicRemove();
  const [academicUpdate] = useAcademicUpdate();
  const onStepClose = () => {
    setStep("");
  };

  const onDelete = () => {
    if (menuData?._id) {
      setDisabled((pre) => !pre);
      academicRemove({
        aid: menuData?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onEditAction = (data) => {
    if (menuData?._id) {
      onStepClose();
      setDisabled((pre) => !pre);
      academicUpdate({
        aid: menuData?._id,
        bodyParameter: data,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      {step === 1 ? (
        <AddSiteAcademicsCourses
          isEdit
          lcid={lcid}
          onEditAction={onEditAction}
          menuData={menuData}
          onClose={onStepClose}
        />
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <h6 onClick={() => setStep(1)}>{t("edit")}</h6>
          <h6 onClick={onDelete}>{t("delete")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default SiteAcademicCoursesMenu;
