import { useTranslation } from "react-i18next";
import style from "../../UiTab.module.css";
import { Link } from "react-router-dom";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
const CustomSquareTabCard = ({
  onNavigate,
  label,
  iconUrl,
  url,
  customStyleContainer,
  state,
  isEdit,
  onMenuToggle,
}) => {
  const { t } = useTranslation();

  return url ? (
    <Link
      className={style.square_tab_card}
      to={url}
      state={state}
      style={customStyleContainer}
    >
      <img
        src={iconUrl}
        alt="card icon"
        className={style.square_tab_card_icon}
      />
      <p>{t(label)}</p>
      {isEdit ? (
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onMenuToggle}
          className={style.square_tab_card_menu}
        />
      ) : null}
    </Link>
  ) : (
    <div
      className={style.square_tab_card}
      style={customStyleContainer}
      onClick={onNavigate}
    >
      <img
        src={iconUrl}
        alt="card icon"
        className={style.square_tab_card_icon}
      />
      <p>{label}</p>
      {isEdit ? (
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onMenuToggle}
          className={style.square_tab_card_menu}
        />
      ) : null}
    </div>
  );
};

export default CustomSquareTabCard;
