import { useState } from "react";
import ModalMenu from "../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import EditHomeOpener from "./EditHomeOpener";
const SiteHomeOpenerCardMenu = ({ onClose, onRefetch, instituteId, data }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState("");

  const onEditStep = () => {
    setStep(1);
  };

  return (
    <>
      {step === 1 ? (
        <EditHomeOpener
          onClose={() => setStep("")}
          onParentClose={onClose}
          onRefetch={onRefetch}
          instituteId={instituteId}
          data={data}
        />
      ) : (
        <ModalMenu onClose={onClose}>
          <h6 onClick={onEditStep}>{t("edit")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default SiteHomeOpenerCardMenu;
