import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../Validation/functions/checkingRequiredField";
import Modal from "../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../Ui/Border/BorderBottom";
import FormInput from "../../Ui/Input/FormInput";
import CreateButton from "../../Ui/Button/CreateButton";
import QvipleLoader from "../../Loader/QvipleLoader";
import style from "../../UserStaffMember/FinanceManager/Income/Income.module.css";
import FormFile from "../../Ui/Input/FormFile";
import { useFileUpload } from "../../../hooks/authentication-api";
import { Dialog } from "@mui/material";
import CustomCrop from "../../../ImageCrop/CustomCrop";
import { useInstituteAddAcademicHead } from "../../../hooks/setting-api";
import FormTextarea from "../../Ui/Input/FormTextarea";
const AddSiteAcademicsCourses = ({
  lcid,
  onClose,
  onRefetch,
  isEdit,
  menuData,
  onEditAction,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    head_name: "",
    head_about: "",
    head_images: [],
  });
  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");
  // const [activeCrop, setActiveCrop] = useState("");

  const [instituteAddAcademicHead] = useInstituteAddAcademicHead();

  const [fileUpload] = useFileUpload();
  useEffect(() => {
    if (isEdit && menuData) {
      setState({
        head_name: menuData?.head_name ?? "",
        head_images: [menuData?.head_images] ?? "",
        head_about: menuData?.head_about ?? "",
      });
    }
  }, [isEdit, menuData]);
  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onCrop = (fl, url) => {
    setOpenCrop(false);
    // if (activeCrop === "NEW_IMAGE") {
    onFileUploadCrop(fl);
    // } else {
    //   onFileEditUploadCrop(fl, activeCrop);
    // }
  };

  const onFileUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            head_images: [...prev.head_images, res.data?.imageKey],
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  // const onFileEditUploadCrop = (data, ind) => {
  //   if (data) {
  //     setDisabled((pre) => !pre);
  //     const formData = new FormData();
  //     formData.append("file", data);
  //     fileUpload({
  //       fileUpload: formData,
  //     })
  //       .then((res) => {
  //         let inp = [...state.head_images];
  //         inp[ind] = res.data?.imageKey;
  //         setState((prev) => ({
  //           ...prev,
  //           head_images: inp,
  //         }));

  //         setDisabled((pre) => !pre);
  //       })
  //       .catch({});
  //   }
  // };
  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
      // setActiveCrop("NEW_IMAGE");
    }
  };

  // const onFileEditUpload = (e) => {
  //   if (e.target.files[0]) {
  //     const image = URL.createObjectURL(e.target.files[0]);
  //     setFileUrl(image);
  //     setOpenCrop(true);
  //   }
  // };

  const onAdd = () => {
    let validation = requiredField(state, ["head_images"]);
    if (existingRequiredField(validation) && lcid) {
      if (isEdit) {
        onEditAction(state);
      } else {
        setDisabled((pre) => !pre);
        instituteAddAcademicHead({
          lcid: lcid,
          bodyParameter: state,
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      {openCrop ? (
        <Dialog open={openCrop}>
          <CustomCrop
            setOpenCrop={setOpenCrop}
            fileUrl={fileUrl}
            first={1}
            second={1}
            onCrop={onCrop}
          />
        </Dialog>
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>
                    {t(
                      isEdit ? "update_academic_course" : "add_academic_course"
                    )}
                  </h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("course_name")}
                placeholder={t("course_name_placeholder")}
                name={"head_name"}
                value={state.head_name}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["head_name"]}
              />
              <FormTextarea
                label={t("course_about")}
                placeholder={t("course_about_placeholder")}
                name={"head_about"}
                value={state.head_about}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["head_about"]}
              />

              {/* {state.head_images?.map((bg, index) => (
                <FormFile
                  key={index}
                  label={t("add_image")}
                  name={`academic${index}`}
                  value={{
                    key: bg,
                    name: "View",
                  }}
                  onChange={(e) => onFileEditUpload(e, index)}
                  errorShow={errorField["image"]}
                />
              ))} */}

              <FormFile
                label={t("add_image")}
                name={"academic"}
                value={
                  state.head_images?.length
                    ? {
                        key: state.head_images?.[0],
                        name: "View",
                      }
                    : ""
                }
                onChange={onFileUpload}
                errorShow={errorField["image"]}
              />

              <CreateButton
                label={isEdit ? "update" : "add"}
                onAction={onAdd}
              />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AddSiteAcademicsCourses;
