import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useState } from "react";
import { useSiteAcademicsHeadSubTopicList } from "../../../../hooks/setting-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import AddSubAcademicDetail from "./AddSubAcademicDetail";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import SubAcademicDetailCard from "./SubAcademicDetailCard";
import Loading from "../../../Loader/Loading";
const SubAcademicDetail = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [add, setAdd] = useState(false);

  const {
    siteAcademicsHeadSubTopicList,
    siteAcademicsHeadSubTopicListLoading,
    siteAcademicsHeadSubTopicListRefetch,
  } = useSiteAcademicsHeadSubTopicList({
    data: {
      anid: getQuery.state?.head?._id,
    },
    skip: !getQuery.state?.head?._id,
  });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  //
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{getQuery.state?.head?.sub_head_title_main ?? ""}</h6>
            <CreateButton
              label="add"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {siteAcademicsHeadSubTopicList?.map((sub) => (
            <SubAcademicDetailCard sub={sub} key={sub?._id} />
          ))}
          {siteAcademicsHeadSubTopicListLoading && <Loading />}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <AddSubAcademicDetail
          onClose={onToggle}
          onRefetch={siteAcademicsHeadSubTopicListRefetch}
          anid={getQuery.state?.head?._id}
        />
      )}
    </>
  );
};

export default SubAcademicDetail;
