import { useTranslation } from "react-i18next";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import {
  useAdministrationRemove,
  useAdministrationUpdate,
} from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import AddAboutUsAdministration from "./AddAboutUsAdministration";
const AboutUsAdministrationCardMenu = ({
  lcid,
  onClose,
  menuData,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");

  const [administrationRemove] = useAdministrationRemove();
  const [administrationUpdate] = useAdministrationUpdate();
  const onStepClose = () => {
    setStep("");
  };

  const onDelete = () => {
    if (menuData?._id && lcid) {
      setDisabled((pre) => !pre);
      administrationRemove({
        lcid: lcid,
        bodyParameter: {
          acid: menuData?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onEditAction = (data) => {
    if (menuData?._id && lcid) {
      onStepClose();
      setDisabled((pre) => !pre);
      administrationUpdate({
        lcid: lcid,
        bodyParameter: {
          ...data,
          acid: menuData?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      {step === 1 ? (
        <AddAboutUsAdministration
          isEdit
          lcid={lcid}
          onEditAction={onEditAction}
          menuData={menuData}
          onClose={onStepClose}
        />
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <h6 onClick={() => setStep(1)}>{t("edit")}</h6>
          <h6 onClick={onDelete}>{t("delete")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default AboutUsAdministrationCardMenu;
