import { useState } from "react";
import ModalMenu from "../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import AddSiteTestimonial from "./AddSiteTestimonial";
import { useInstituteUpdateTestitmonial } from "../../../hooks/setting-api";

const SiteTestimonialCardMenu = ({
  onClose,
  monial,
  onRefetch,
  instituteId,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");

  const [instituteUpdateTestitmonial] = useInstituteUpdateTestitmonial();
  const onEditStep = () => {
    setStep(1);
  };

  const onEdit = (data) => {
    setStep("");
    if (monial?._id && data) {
      setDisabled((pre) => !pre);
      instituteUpdateTestitmonial({
        id: instituteId,
        tid: monial?._id,
        bodyParameter: data,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      {step === 1 ? (
        <AddSiteTestimonial
          onClose={() => setStep("")}
          isEdit
          onEdit={onEdit}
          monail={monial}
        />
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          {/* <h6 onClick={onRemove}>{t("delete")}</h6> */}
          <h6 onClick={onEditStep}>{t("edit")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default SiteTestimonialCardMenu;
