import { imageShowUrl } from "../../../../services/BaseUrl";
import { useTranslation } from "react-i18next";
import style from "../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useState } from "react";
import AboutUsAdministrationCardMenu from "./AboutUsAdministrationCardMenu";

const AboutUsAdministrationCard = ({ admin, lcid, onRefetch }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);
  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div
        className={style.sf_card}
        style={{
          alignItems: "flex-start",
        }}
      >
        <div
          className={style.sf_card_contatiner}
          style={{
            alignItems: "flex-start",
          }}
        >
          {admin?.leading_person?.profilePhoto ? (
            <img
              src={`${imageShowUrl}/${admin?.leading_person?.profilePhoto}`}
              style={{
                borderRadius: "6px",
                width: "5rem",
                height: "5rem",
              }}
              alt="user avatar"
            />
          ) : (
            <img
              src={`${assestsFeedUrl}/feed-user-avatar.svg`}
              style={{
                borderRadius: "6px",
                width: "5rem",
                height: "5rem",
              }}
              alt="user avatar"
            />
          )}

          <div className={style.sf_card_contatiner_inner}>
            <h6
              style={{
                marginBottom: "0",
              }}
            >
              {t("designation_name")}
              {": "}
              {admin?.leading_person_position ?? ""}
            </h6>
            <p>
              {t("name")} {": "}
              {admin?.leading_person?.userLegalName ?? ""}
            </p>
            <p>
              {t("message")} {": "}
              {admin?.leading_person_message ?? ""}
            </p>
          </div>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onToggle}
        />
      </div>
      {open && (
        <AboutUsAdministrationCardMenu
          onClose={onToggle}
          lcid={lcid}
          menuData={admin}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default AboutUsAdministrationCard;
