import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../Validation/functions/checkingRequiredField";
import Modal from "../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../Ui/Border/BorderBottom";
import FormInput from "../../Ui/Input/FormInput";
import CreateButton from "../../Ui/Button/CreateButton";
import QvipleLoader from "../../Loader/QvipleLoader";
import style from "../../UserStaffMember/FinanceManager/Income/Income.module.css";
import FormFile from "../../Ui/Input/FormFile";
import { useFileUpload } from "../../../hooks/authentication-api";
import { Dialog } from "@mui/material";
import CustomCrop from "../../../ImageCrop/CustomCrop";
import { useInstituteAddTestitmonial } from "../../../hooks/setting-api";
import FormTextarea from "../../Ui/Input/FormTextarea";
const AddSiteTestimonial = ({
  instituteId,
  onClose,
  onRefetch,
  isEdit,
  monail,
  onEdit,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    linkUrl: "",
    image: "",
    name: "",
    bio: "",
  });
  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");
  const [instituteAddTestitmonial] = useInstituteAddTestitmonial();

  const [fileUpload] = useFileUpload();

  useEffect(() => {
    if (isEdit) {
      setState({
        linkUrl: monail?.link ?? "",
        image: monail?.image ?? "",
        name: monail?.name ?? "",
        bio: monail?.bio ?? "",
      });
    }
  }, [isEdit, monail]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onCrop = (fl, url) => {
    setOpenCrop(false);
    onFileUploadCrop(fl);
  };

  const onFileUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            image: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
    }
  };

  const onAdd = () => {
    let validation = requiredField(state, ["bio"]);
    if (existingRequiredField(validation)) {
      if (isEdit) {
        onEdit({
          link: state.linkUrl,
          image: state.image,
          name: state.name,
          bio: state.bio,
        });
      } else {
        if (instituteId) {
          setDisabled((pre) => !pre);
          instituteAddTestitmonial({
            id: instituteId,
            bodyParameter: {
              test: [
                {
                  link: state.linkUrl,
                  image: state.image,
                  name: state.name,
                  bio: state.bio,
                },
              ],
            },
          })
            .then(() => {
              onRefetch();
              setDisabled((pre) => !pre);
              onClose();
            })
            .catch({});
        }
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      {openCrop ? (
        <Dialog open={openCrop}>
          <CustomCrop
            setOpenCrop={setOpenCrop}
            fileUrl={fileUrl}
            first={1}
            second={1}
            onCrop={onCrop}
          />
        </Dialog>
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>
                    {t(isEdit ? "update_testimonial" : "add_testimonial")}
                  </h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("name")}
                placeholder={t("name_placeholder")}
                name={"name"}
                value={state.name}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["name"]}
              />
              <FormTextarea
                label={t("bio")}
                placeholder={t("bio_placeholder")}
                name={"bio"}
                value={state.bio}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["bio"]}
              />
              <FormInput
                label={t("linkUrl")}
                placeholder={t("linkUrl_placeholder")}
                name={"linkUrl"}
                value={state.linkUrl}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["linkUrl"]}
              />

              <FormFile
                label={t("add_image")}
                name={"testimonial"}
                value={
                  state.image
                    ? {
                        key: state.image,
                        name: "View",
                      }
                    : ""
                }
                onChange={onFileUpload}
                errorShow={errorField["image"]}
              />
              <CreateButton
                label={isEdit ? "update" : "add"}
                onAction={onAdd}
              />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AddSiteTestimonial;
