import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import { useFileUpload } from "../../../hooks/authentication-api";
import {
  existingRequiredField,
  requiredField,
} from "../../Validation/functions/checkingRequiredField";
import QvipleLoader from "../../Loader/QvipleLoader";
import { Dialog } from "@mui/material";
import CustomCrop from "../../../ImageCrop/CustomCrop";
import CreateButton from "../../Ui/Button/CreateButton";
import FormFile from "../../Ui/Input/FormFile";
import FormInput from "../../Ui/Input/FormInput";
import {
  useSiteHomeBackground,
  useWebsiteInfoGetByInstitute,
} from "../../../hooks/setting-api";
const HomeBackgroundTheme = ({ instituteId, lcid }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    images: [],
    color_theme: "",
  });
  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");
  const [activeCrop, setActiveCrop] = useState("");

  const [fileUpload] = useFileUpload();
  const [siteHomeBackground] = useSiteHomeBackground();

  const { websiteInfoGetByInstitute, websiteInfoGetByInstituteRefetch } =
    useWebsiteInfoGetByInstitute({
      id: instituteId,
      skip: !instituteId,
    });

  useEffect(() => {
    if (instituteId) {
      websiteInfoGetByInstituteRefetch();
    }
  }, [instituteId]);

  useEffect(() => {
    if (
      websiteInfoGetByInstitute?.one_ins?.landing_control
        ?.home_background_object
    ) {
      let st =
        websiteInfoGetByInstitute?.one_ins?.landing_control
          ?.home_background_object;
      setState({
        images: st?.images ?? "",
        color_theme: st?.color_theme ?? "",
      });
    }
  }, [
    websiteInfoGetByInstitute?.one_ins?.landing_control?.home_background_object,
  ]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onCrop = (fl, url) => {
    setOpenCrop(false);
    if (activeCrop === "BG_IMAGE") {
      onFileBgUploadCrop(fl);
    } else {
      onFileEditUploadCrop(fl, activeCrop);
    }
  };
  const onFileBgUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            setState((prev) => ({
              ...prev,
              images: [...prev?.images, res.data?.imageKey],
            }));
          }
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onFileBgUpload = (e) => {
    if (e.target.files?.length) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
      setActiveCrop("BG_IMAGE");
    }
  };

  const onFileEditUploadCrop = (data, ind) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            let inp = [...state.images];
            inp[ind] = res.data?.imageKey;
            setState((prev) => ({
              ...prev,
              images: inp,
            }));
          }
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onFileEditUpload = (e, ind) => {
    if (e.target.files?.length) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
      setActiveCrop(ind);
    }
  };

  const onUpdate = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && lcid) {
      setDisabled((pre) => !pre);
      siteHomeBackground({
        lcid: lcid,
        bodyParameter: {
          home_background_object: state,
        },
      })
        .then(() => {
          websiteInfoGetByInstituteRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  // console.info("fdsgfsf", state);

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("background_theme")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          <div className={style.ossa_form}>
            {state.images?.map((bg, index) => (
              <FormFile
                label={t("institute_backgroud")}
                name={`background_image${index}`}
                value={{
                  key: bg,
                  name: "View",
                }}
                onChange={(e) => onFileEditUpload(e, index)}
                errorShow={errorField["organisation_structure"]}
                customFormFileContainer={{
                  width: "49%",
                }}
              />
            ))}
            <FormFile
              label={t("institute_backgroud")}
              name={`background_image`}
              value=""
              onChange={onFileBgUpload}
              customFormFileContainer={{
                width: "49%",
              }}
            />
            <FormInput
              label={t("choose_site_theme")}
              placeholder={t("institute_name_placeholder")}
              name={"color_theme"}
              value={state?.color_theme}
              type={"color"}
              onChange={onChange}
              errorShow={errorField["color_theme"]}
              customFormInputContainer={{ width: "49%" }}
              customFormInput={{
                width: "100%",
              }}
            />
          </div>

          <CreateButton label="update" onAction={onUpdate} />
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {openCrop ? (
        <Dialog open={openCrop}>
          <CustomCrop
            setOpenCrop={setOpenCrop}
            fileUrl={fileUrl}
            first={18}
            second={6}
            onCrop={onCrop}
          />
        </Dialog>
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default HomeBackgroundTheme;
