import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import {
  useSiteAboutUsAboutSociety,
  useWebsiteInfoGetByInstitute,
} from "../../../hooks/setting-api";
import CreateButton from "../../Ui/Button/CreateButton";
import FormInput from "../../Ui/Input/FormInput";
import FormFile from "../../Ui/Input/FormFile";
import QvipleLoader from "../../Loader/QvipleLoader";
import { useFileUpload } from "../../../hooks/authentication-api";
import { Dialog } from "@mui/material";
import CustomCrop from "../../../ImageCrop/CustomCrop";
import {
  existingRequiredField,
  requiredField,
} from "../../Validation/functions/checkingRequiredField";
import FormTextarea from "../../Ui/Input/FormTextarea";
const AboutUsSociety = ({ instituteId, lcid }) => {
  const { t } = useTranslation();
  const { websiteInfoGetByInstitute, websiteInfoGetByInstituteRefetch } =
    useWebsiteInfoGetByInstitute({
      id: instituteId,
      skip: !instituteId,
    });
  useEffect(() => {
    if (instituteId) websiteInfoGetByInstituteRefetch();
  }, [instituteId, websiteInfoGetByInstituteRefetch]);

  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    name: "",
    vision: "",
    mission: "",
    organisation_structure: "",
    founder_message_image: "",
    founder_message_designation: "",
    founder_message_message: "",
    founder_message_name: "",
    about: "",
  });
  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");

  const [siteAboutUsAboutSociety] = useSiteAboutUsAboutSociety();

  const [fileUpload] = useFileUpload();

  useEffect(() => {
    if (
      websiteInfoGetByInstitute?.one_ins?.landing_control?.about_society_dynamic
    ) {
      let st =
        websiteInfoGetByInstitute?.one_ins?.landing_control
          ?.about_society_dynamic;
      setState({
        name: st?.dynamic_name ?? "",
        vision: st?.vision ?? "",
        mission: st?.mission ?? "",
        organisation_structure: st?.organisation_structure ?? "",
        founder_message_image: st?.founder_message_image ?? "",
        founder_message_designation: st?.founder_message_designation ?? "",
        founder_message_message: st?.founder_message_message ?? "",
        founder_message_name: st?.founder_message_name ?? "",
        about: st?.about ?? "",
      });
    }
  }, [
    websiteInfoGetByInstitute?.one_ins?.landing_control?.about_society_dynamic,
  ]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onCrop = (fl, url) => {
    setOpenCrop(false);
    onFileUploadCrop(fl);
  };

  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
    }
  };
  const onFileUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            founder_message_image: res.data?.imageKey,
          }));
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onOrganisationStructure = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            organisation_structure: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && lcid) {
      setDisabled((pre) => !pre);
      siteAboutUsAboutSociety({
        lcid: lcid,
        bodyParameter: state,
      })
        .then(() => {
          websiteInfoGetByInstituteRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("about_society")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          <div className={style.ossa_form}>
            <FormInput
              label={t("society_name")}
              placeholder={t("society_name_placeholder")}
              name={"name"}
              value={state.name}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["name"]}
              customFormInputContainer={{ width: "100%" }}
            />
            <FormTextarea
              label={t("society_about")}
              placeholder={t("society_about_placeholder")}
              name={"about"}
              value={state.about}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["about"]}
              customFormInputContainer={{ width: "100%" }}
            />

            <FormTextarea
              label={t("society_vision")}
              placeholder={t("society_vision_placeholder")}
              name={"vision"}
              value={state.vision}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["vision"]}
              customFormInputContainer={{ width: "100%" }}
            />
            <FormTextarea
              label={t("society_mission")}
              placeholder={t("society_mission_placeholder")}
              name={"mission"}
              value={state.mission}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["mission"]}
              customFormInputContainer={{ width: "100%" }}
            />
            <FormFile
              label={t("society_structure")}
              name={"society_structure"}
              value={
                state.organisation_structure
                  ? {
                      key: state.organisation_structure,
                      name: "View",
                    }
                  : ""
              }
              onChange={onOrganisationStructure}
              errorShow={errorField["organisation_structure"]}
              customFormFileContainer={{
                width: "100%",
              }}
            />
            <FormInput
              label={t("founder_name")}
              placeholder={t("founder_name_placeholder")}
              name={"founder_message_name"}
              value={state.founder_message_name}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["founder_message_name"]}
              customFormInputContainer={{ width: "100%" }}
            />
            <FormInput
              label={t("founder_designation_name")}
              placeholder={t("founder_designation_name_placeholder")}
              name={"founder_message_designation"}
              value={state.founder_message_designation}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["founder_message_designation"]}
              customFormInputContainer={{ width: "100%" }}
            />
            <FormFile
              label={t("founder_image")}
              name={"founder_message_image"}
              value={
                state.founder_message_image
                  ? {
                      key: state.founder_message_image,
                      name: "View",
                    }
                  : ""
              }
              onChange={onFileUpload}
              errorShow={errorField["founder_message_image"]}
              customFormFileContainer={{
                width: "100%",
              }}
            />
            <FormTextarea
              label={t("founder_message")}
              placeholder={t("founder_message_placeholder")}
              name={"founder_message_message"}
              value={state.founder_message_message}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["founder_message_message"]}
              customFormInputContainer={{ width: "100%" }}
            />
          </div>

          <CreateButton label="update" onAction={onAdd} />
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {openCrop ? (
        <Dialog open={openCrop}>
          <CustomCrop
            setOpenCrop={setOpenCrop}
            fileUrl={fileUrl}
            first={1}
            second={1}
            onCrop={onCrop}
          />
        </Dialog>
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AboutUsSociety;
