import { useTranslation } from "react-i18next";
import style from "../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import { useState } from "react";
import SiteTestimonialCardMenu from "./SiteTestimonialCardMenu";
const SiteTestimonialCard = ({ monial, onRefetch, instituteId }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);

  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div
        className={style.sf_card}
        style={{
          alignItems: "flex-start",
        }}
      >
        <div
          className={style.sf_card_contatiner}
          style={{
            alignItems: "flex-start",
          }}
        >
          <img
            src={
              monial?.image
                ? `${imageShowUrl}/${monial?.image}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            style={{
              borderRadius: "6px",
              width: "5rem",
              height: "5rem",
            }}
            alt="user avatar"
          />
          <div className={style.sf_card_contatiner_inner}>
            <h6
              style={{
                marginBottom: "0",
              }}
            >
              {t("name")}
              {": "}
              {monial?.name}
            </h6>
            <p
              style={{
                marginBottom: "0",
              }}
            >
              {t("link")}
              {": "}
              <a
                href={monial?.link}
                target="_blank"
                rel="noopener noreferrer"
                style={{
                  textDecoration: "none",
                  color: "#0942a6",
                }}
              >
                {monial?.link ?? ""}
              </a>
            </p>
            <p
              style={{
                whiteSpace: "break-spaces",
              }}
            >
              {monial?.bio}
            </p>
          </div>
        </div>

        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onToggle}
          style={{
            cursor: "pointer",
            height: "1.5rem",
            width: "auto",
          }}
        />
      </div>

      {open && (
        <SiteTestimonialCardMenu
          onRefetch={onRefetch}
          onClose={onToggle}
          monial={monial}
          instituteId={instituteId}
        />
      )}
    </>
  );
};

export default SiteTestimonialCard;
