import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../Ui/Border/BorderBottom";
import { Fragment, useEffect, useState } from "react";
import {
  useSiteAboutUsAboutInstitute,
  useWebsiteInfoGetByInstitute,
} from "../../../hooks/setting-api";
import CreateButton from "../../Ui/Button/CreateButton";
import FormInput from "../../Ui/Input/FormInput";
import FormFile from "../../Ui/Input/FormFile";
import QvipleLoader from "../../Loader/QvipleLoader";
import { useFileUpload } from "../../../hooks/authentication-api";
import {
  existingRequiredField,
  requiredField,
} from "../../Validation/functions/checkingRequiredField";
import FormTextarea from "../../Ui/Input/FormTextarea";
const AboutUsInstituteAbout = ({ instituteId, lcid }) => {
  const { t } = useTranslation();
  const { websiteInfoGetByInstitute, websiteInfoGetByInstituteRefetch } =
    useWebsiteInfoGetByInstitute({
      id: instituteId,
      skip: !instituteId,
    });
  useEffect(() => {
    if (instituteId) websiteInfoGetByInstituteRefetch();
  }, [instituteId, websiteInfoGetByInstituteRefetch]);

  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    ins_name: "",
    ins_about: "",
    vision: "",
    mission: "",
    org_structure: "",
    affiliation: [
      {
        name: "",
        info: "",
      },
    ],
  });

  const [siteAboutUsAboutInstitute] = useSiteAboutUsAboutInstitute();

  const [fileUpload] = useFileUpload();

  useEffect(() => {
    if (
      websiteInfoGetByInstitute?.one_ins?.landing_control
        ?.about_us_institute_object
    ) {
      let st =
        websiteInfoGetByInstitute?.one_ins?.landing_control
          ?.about_us_institute_object;
      let a = [];
      for (let ft of st?.affiliation) {
        a.push({
          name: ft?.name ?? "",
          info: ft?.info ?? "",
        });
      }
      setState({
        ins_name: st?.ins_name ?? "",
        ins_about: st?.ins_about ?? "",
        vision: st?.vision ?? "",
        mission: st?.mission ?? "",
        org_structure: st?.org_structure ?? "",
        affiliation: a,
      });
    }
  }, [
    websiteInfoGetByInstitute?.one_ins?.landing_control
      ?.about_us_institute_object,
  ]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onAffilicationChange = (e, ind) => {
    let p = [...state.affiliation];
    p[ind][e.target.name] = e.target.value;
    setState((prev) => ({
      ...prev,
      affiliation: p,
    }));
  };

  const onOrganisationStructure = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            org_structure: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && lcid) {
      setDisabled((pre) => !pre);
      siteAboutUsAboutInstitute({
        lcid: lcid,
        bodyParameter: { about_us_institute_object: state },
      })
        .then(() => {
          websiteInfoGetByInstituteRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("about_institute")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          <div className={style.ossa_form}>
            <FormInput
              label={t("institute_name")}
              placeholder={t("institute_name_placeholder")}
              name={"ins_name"}
              value={state.ins_name}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["ins_name"]}
              customFormInputContainer={{ width: "100%" }}
            />
            <FormTextarea
              label={t("institute_about")}
              placeholder={t("institute_about_placeholder")}
              name={"ins_about"}
              value={state.ins_about}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["ins_about"]}
              customFormInputContainer={{ width: "100%" }}
            />

            <FormTextarea
              label={t("institute_vision")}
              placeholder={t("institute_vision_placeholder")}
              name={"vision"}
              value={state.vision}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["vision"]}
              customFormInputContainer={{ width: "100%" }}
            />
            <FormTextarea
              label={t("institute_mission")}
              placeholder={t("institute_mission_placeholder")}
              name={"mission"}
              value={state.mission}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["mission"]}
              customFormInputContainer={{ width: "100%" }}
            />

            <FormFile
              label={t("organsiation_structure")}
              name={"organsiation_structure"}
              value={
                state.org_structure
                  ? {
                      key: state.org_structure,
                      name: "View",
                    }
                  : ""
              }
              onChange={onOrganisationStructure}
              errorShow={errorField["org_structure"]}
              customFormFileContainer={{
                width: "100%",
              }}
            />
            {state.affiliation?.map((aff, index) => (
              <Fragment key={index}>
                <FormInput
                  label={t("affiliation_name")}
                  placeholder={t("affiliation_name_placeholder")}
                  name={"name"}
                  value={aff?.name}
                  type={"text"}
                  onChange={(e) => onAffilicationChange(e, index)}
                  customFormInputContainer={{ width: "49%" }}
                />
                <FormTextarea
                  label={t("affiliation_info")}
                  placeholder={t("affiliation_info_placeholder")}
                  name={"info"}
                  value={aff?.info}
                  type={"text"}
                  onChange={(e) => onAffilicationChange(e, index)}
                  customFormInputContainer={{ width: "49%" }}
                />
              </Fragment>
            ))}
          </div>

          <CreateButton label="update" onAction={onAdd} />
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AboutUsInstituteAbout;
