import { useTranslation } from "react-i18next";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import {
  useAcademicNestedRemove,
  useAcademicNestedUpdate,
} from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import AddSiteAcademicCoursesDetail from "./AddSiteAcademicCoursesDetail";
const SiteAcademicCoursesDetailMenu = ({
  acid,
  onClose,
  menuData,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");

  const [academicNestedRemove] = useAcademicNestedRemove();
  const [academicNestedUpdate] = useAcademicNestedUpdate();
  const onStepClose = () => {
    setStep("");
  };

  const onDelete = () => {
    if (menuData?._id) {
      setDisabled((pre) => !pre);
      academicNestedRemove({
        anid: menuData?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onEditAction = (data) => {
    if (menuData?._id) {
      onStepClose();
      setDisabled((pre) => !pre);
      academicNestedUpdate({
        anid: menuData?._id,
        bodyParameter: data,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  //   console.info("Sfdsgfsd", menuData);
  return (
    <>
      {step === 1 ? (
        <AddSiteAcademicCoursesDetail
          acid={acid}
          onClose={onStepClose}
          isEdit
          onEditAction={onEditAction}
          menuData={menuData}
        />
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <h6 onClick={() => setStep(1)}>{t("edit")}</h6>
          <h6 onClick={onDelete}>{t("delete")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default SiteAcademicCoursesDetailMenu;
