import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import {
  useInstituteUpdateAffiliation,
  useWebsiteInfoGetByInstitute,
} from "../../../hooks/setting-api";
import CreateButton from "../../Ui/Button/CreateButton";
// import AddSiteAffiliation from "./AddSiteAffiliation";
import FormInput from "../../Ui/Input/FormInput";
import FormFile from "../../Ui/Input/FormFile";
import QvipleLoader from "../../Loader/QvipleLoader";
import { useFileUpload } from "../../../hooks/authentication-api";
import { Dialog } from "@mui/material";
import CustomCrop from "../../../ImageCrop/CustomCrop";
import {
  existingRequiredField,
  requiredField,
} from "../../Validation/functions/checkingRequiredField";
const SiteAffiliation = ({ instituteId, lcid }) => {
  const { t } = useTranslation();
  // const [add, setAdd] = useState(false);

  const { websiteInfoGetByInstitute, websiteInfoGetByInstituteRefetch } =
    useWebsiteInfoGetByInstitute({
      id: instituteId,
      skip: !instituteId,
    });
  useEffect(() => {
    if (instituteId) websiteInfoGetByInstituteRefetch();
  }, [instituteId, websiteInfoGetByInstituteRefetch]);

  // const onToggle = () => {
  //   setAdd((pre) => !pre);
  // };

  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    name: "",
    logo: [],
  });
  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");
  const [activeCrop, setActiveCrop] = useState("");

  const [instituteUpdateAffiliation] = useInstituteUpdateAffiliation();

  const [fileUpload] = useFileUpload();

  useEffect(() => {
    if (websiteInfoGetByInstitute?.one_ins?.landing_control) {
      setState({
        name:
          websiteInfoGetByInstitute?.one_ins?.landing_control
            ?.affiliation_name ?? "",
        logo:
          websiteInfoGetByInstitute?.one_ins?.landing_control?.affiliation_logo
            ?.length > 0
            ? websiteInfoGetByInstitute?.one_ins?.landing_control
                ?.affiliation_logo
            : [],
      });
    }
  }, [websiteInfoGetByInstitute?.one_ins?.landing_control]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onCrop = (fl, url) => {
    setOpenCrop(false);
    if (activeCrop === "NEW_IMAGE") {
      onFileUploadCrop(fl);
    } else {
      onFileEditUploadCrop(fl, activeCrop);
    }
  };

  const onFileUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            logo: [...prev.logo, res.data?.imageKey],
          }));
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onFileEditUploadCrop = (data, ind) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          let inp = [...state.logo];
          inp[ind] = res.data?.imageKey;
          setState((prev) => ({
            ...prev,
            logo: inp,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
      setActiveCrop("NEW_IMAGE");
    }
  };

  const onFileEditUpload = (e, ind) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
      setActiveCrop(ind);
    }
  };

  const onAdd = () => {
    let validation = requiredField(state, ["logo"]);
    if (existingRequiredField(validation) && lcid) {
      setDisabled((pre) => !pre);
      instituteUpdateAffiliation({
        lcid: lcid,
        bodyParameter: state,
      })
        .then(() => {
          websiteInfoGetByInstituteRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("affiliation")}</h6>
            {/* {websiteInfoGetByInstitute?.one_ins?.iso_certificate?.length <= 2 ? ( */}
            {/* <CreateButton
              label="add"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            /> */}
            {/* ) : null} */}
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          <div className={style.ossa_form}>
            <FormInput
              label={t("affiliation_name")}
              placeholder={t("affiliation_name_placeholder")}
              name={"name"}
              value={state.name}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["name"]}
              customFormInputContainer={{ width: "49%" }}
            />
            {state.logo?.map((bg, index) => (
              <FormFile
                key={index}
                label={t("add_image")}
                name={`home-opener${index}`}
                value={{
                  key: bg,
                  name: "View",
                }}
                onChange={(e) => onFileEditUpload(e, index)}
                errorShow={errorField["image"]}
                customFormFileContainer={{
                  width: "49%",
                }}
              />
            ))}

            <FormFile
              label={t("add_image")}
              name={"home-opener"}
              value=""
              onChange={onFileUpload}
              errorShow={errorField["image"]}
              customFormFileContainer={{
                width: "49%",
              }}
            />
          </div>

          <CreateButton label="update" onAction={onAdd} />

          {/* {websiteInfoGetByInstitute?.one_ins?.landing_control?.gallery?.map(
            (gall) => (
              <ImageGallaryCard imageKey={gall?.image} key={gall?._id} />
            )
          )} */}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {/* {disabled && <QvipleLoader />} */}
      {/* {add && (
        <AddSiteAffiliation
          onClose={onToggle}
          lcid={lcid}
          onRefetch={websiteInfoGetByInstituteRefetch}
        />
      )} */}
      {openCrop ? (
        <Dialog open={openCrop}>
          <CustomCrop
            setOpenCrop={setOpenCrop}
            fileUrl={fileUrl}
            first={1}
            second={1}
            onCrop={onCrop}
          />
        </Dialog>
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default SiteAffiliation;
