import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInput from "../../../Ui/Input/FormInput";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import style from "../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { useSiteAboutUsAdministration } from "../../../../hooks/setting-api";
import FormTextarea from "../../../Ui/Input/FormTextarea";
import AssignStaffModal from "../../../../Department/Institute/AssignStaff/AssignStaffModal";
import styleEdit from "../../../../Profile/Institute/Pages/Site/SiteEdit.module.css";
import { imageShowUrl } from "../../../../services/BaseUrl";

const AddAboutUsAdministration = ({
  onClose,
  onRefetch,
  lcid,
  isEdit,
  onEditAction,
  menuData,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    leading_person: "",
    leading_person_position: "",
    leading_person_message: "",
  });
  const [assignHead, setAssignHead] = useState(false);
  const [assignHeadStaff, setAssignHeadStaff] = useState("");

  const [siteAboutUsAdministration] = useSiteAboutUsAdministration();

  useEffect(() => {
    if (isEdit && menuData) {
      setState({
        leading_person: menuData?.leading_person?._id ?? "",
        leading_person_position: menuData?.leading_person_position ?? "",
        leading_person_message: menuData?.leading_person_message ?? "",
      });
      setAssignHeadStaff(menuData?.leading_person);
    }
  }, [isEdit, menuData]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onSelectLeading = (assing) => {
    setAssignHeadStaff(assing?.user);
    setState((prev) => ({
      ...prev,
      leading_person: assing?.user._id,
    }));
  };

  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && lcid) {
      if (isEdit) {
        onEditAction(state);
      } else {
        setDisabled((pre) => !pre);
        siteAboutUsAdministration({
          lcid: lcid,
          bodyParameter: {
            admins: [state],
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      {assignHead ? (
        <AssignStaffModal
          assignHead={assignHead}
          headSelect={onSelectLeading}
          title={t("select_leading_person")}
          onClose={() => setAssignHead((pre) => !pre)}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>
                    {t(isEdit ? "update_administration" : "add_administration")}
                  </h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("designation_name")}
                placeholder={t("designation_name_placeholder")}
                name={"leading_person_position"}
                value={state.leading_person_position}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["leading_person_position"]}
              />

              <div
                className={styleEdit.leading_person_container}
                onClick={() => setAssignHead(true)}
                style={{
                  marginTop: "1rem",
                  width: "100%",
                }}
              >
                <img
                  src={
                    assignHeadStaff?.profilePhoto
                      ? `${imageShowUrl}/${assignHeadStaff.profilePhoto}`
                      : "/images/user-icon.svg"
                  }
                  alt="avatar"
                />
                <div className={styleEdit.leading_person_text}>
                  <h6>
                    {assignHeadStaff
                      ? assignHeadStaff?.userLegalName
                      : t("select_staff")}
                  </h6>
                  <p>
                    {t("search_joined_list")}{" "}
                    {errorField["leading_person"] && (
                      <span style={{ color: "red", marginLeft: "15px" }}>
                        * {t("form_require_label")}
                      </span>
                    )}
                  </p>
                </div>
              </div>
              <FormTextarea
                label={t("message")}
                placeholder={t("message_placeholder")}
                name={"leading_person_message"}
                value={state.leading_person_message}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["leading_person_message"]}
              />

              <CreateButton
                label={isEdit ? "update" : "add"}
                onAction={onAdd}
              />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AddAboutUsAdministration;
