import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import { useWebsiteInfoGetByInstitute } from "../../../../hooks/setting-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useLocation, useParams, useSearchParams } from "react-router-dom";
import CustomSquareTabCard from "../../../Ui/Tab/Square/CustomSquareTabCard";
import SquareTabWrapper from "../../../Ui/Tab/Square/SquareTabWrapper";
import AddHomeAccreditation from "./AddHomeAccreditation";
import HomeAccreditationDetailt from "./HomeAccreditationDetailt";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import HomeAccreditationMenu from "./HomeAccreditationMenu";

const HomeAccreditation = ({ instituteId, lcid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [open, setOpen] = useState("");

  const { websiteInfoGetByInstitute, websiteInfoGetByInstituteRefetch } =
    useWebsiteInfoGetByInstitute({
      id: instituteId,
      skip: !instituteId,
    });
  useEffect(() => {
    if (instituteId) websiteInfoGetByInstituteRefetch();
  }, [instituteId, websiteInfoGetByInstituteRefetch]);

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const params = useParams();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  let measure = document.getElementById("562nbtyjnsd")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    let disvible =
      measure > 820 ? (measure > 1100 ? 6 : 5) : measure > 700 ? 4 : 3;
    let mi = measure > 820 ? 0 : 2;
    width = `${Math.floor((measure - mi - 31 * disvible - 1) / disvible)}px`;
    height = width;
  }

  const onMenuToggle = (e, data) => {
    e.preventDefault();
    setOpen(data);
  };
  //
  return (
    <>
      {searchParams.get("n") === "detail" ? (
        <HomeAccreditationDetailt instituteId={instituteId} lcid={lcid} />
      ) : (
        <UserMemberContainerWrapper>
          <UserMemberContentWrapper>
            <div className={style.fm_header_container}>
              <h6>{t("accreditation")}</h6>
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper>
            {!searchParams.get("n") ? (
              <SquareTabWrapper
                dynamicId="562nbtyjnsd"
                customStyle={{
                  padding: "0",
                }}
              >
                {websiteInfoGetByInstitute?.one_ins?.landing_control?.home_accreditation_object?.map(
                  (course) => (
                    <CustomSquareTabCard
                      key={course?._id}
                      label={course?.name}
                      iconUrl={`${imageShowUrl}/${course?.image}`}
                      url={`/q/${params.username}/other/site?type=home-accreditation&n=detail`}
                      state={{
                        accrediation: course,
                      }}
                      customStyleContainer={{
                        height: height,
                        width: width,
                      }}
                      isEdit
                      onMenuToggle={(e) => onMenuToggle(e, course)}
                    />
                  )
                )}
              </SquareTabWrapper>
            ) : null}
          </UserMemberContentWrapper>
        </UserMemberContainerWrapper>
      )}

      {/* {disabled && <QvipleLoader />} */}
      {add && (
        <AddHomeAccreditation
          onClose={onToggle}
          lcid={lcid}
          onRefetch={websiteInfoGetByInstituteRefetch}
        />
      )}
      {open && (
        <HomeAccreditationMenu
          onClose={() => setOpen("")}
          lcid={lcid}
          menuData={open}
          onRefetch={websiteInfoGetByInstituteRefetch}
        />
      )}
    </>
  );
};

export default HomeAccreditation;
